ul{
    padding: 0;
    list-style: none;
}
.header{
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 157px;
    border: 1px solid white;
    display: flex;
    flex-direction: column;
    backdrop-filter: blur(5px);
}
.head{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
}
.more{
    display: none;
    padding: 5px;
    background-color: rgb(0, 0, 0);
    color: yellow;
}
.less{
    display: none;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 70px;
}
.links{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.slide{
    
    transition: height 1s ease;
    position: absolute;
    top: 0;
    height: 100vh;
    width: 100%;
}
@media screen and (max-width: 375px) {
    .header{
        top: 0;
        right: 0;
        height: 70px;
        width: 100%;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        
    }
    .less{
        display: flex;
    }
    .slide{
        background-color: rgba(22, 22, 22, 0.678);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        height: 0;
        overflow: hidden;
        
    }
    .more{
        display: block;
    }
    .head{
        
    }
    .links{
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
}
@media screen and (min-width: 376px)  {
    .header{
        justify-content: space-between;
        
    }
    .links{
        display: flex;
    }
    .slide{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        padding: 35px 0 35px 35px;
    }
}
